<template>
    <div>
      <h1>IMAÁ∫¢ÈªëÊ¶ú</h1>
      <div v-for="course in courses" :key="course.id" class="course-item">
        <h3>{{ course.name }}</h3>
        <button @click="upvote(course.id)">üëç {{ course.upvotes }}</button>
        <button @click="downvote(course.id)">üëé {{ course.downvotes }}</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        courses: [], // ÂàùÂßãËØæÁ®ãÂàóË°®
      };
    },
    created() {
      // Âä†ËΩΩËØæÁ®ãÊï∞ÊçÆÔºåÂèØ‰ª•ÊõøÊç¢‰∏∫‰ªéÊñá‰ª∂Êàñ API Ëé∑ÂèñÁöÑÊï∞ÊçÆ
      fetch('https://n3xta.github.io/ima-red-black-list/public/courses.json')
        .then((response) => response.json())
        .then((data) => {
          // ÂàùÂßãÂåñÁÇπËµûÂíåÁÇπË∏©Êï∞ÊçÆ
          this.courses = data.map((course) => ({
            ...course,
            upvotes: 0,
            downvotes: 0,
          }));
        });
    },
    methods: {
      upvote(id) {
        const course = this.courses.find((c) => c.id === id);
        course.upvotes++;
      },
      downvote(id) {
        const course = this.courses.find((c) => c.id === id);
        course.downvotes++;
      },
    },
  };
  </script>
  
  <style scoped>
  .course-item {
    margin: 10px 0;
    display: flex;
    align-items: center;
  }
  
  button {
    margin-left: 10px;
  }
  </style>
  